@charset "UTF-8";  
body {  
  font-family: "Arial", sans-serif;  
  background-color: hsl(24deg 45.45% 97.84%);  
  margin: 0;  
  padding: 20px;  
}  

.mainContainer {  
  max-width: calc(100% - 100px);  
  margin: 0 auto;  
}  
.popup {  
  position: fixed;  
  top: 0;  
  left: 0;  
  right: 0;  
  bottom: 0;  
  background-color: rgba(0, 0, 0, 0.5);  
  display: flex;  
  justify-content: center;  
  align-items: center;  
  z-index: 1000;  
}  

.popup-content {  
  background-color: white;  
  border-radius: 8px;  
  padding: 20px;  
  max-width: 520px;
  width: 100%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);  
  text-align: center;  
}  

.popup-header {  
  display: flex;  
  align-items: center;  
  justify-content: center;  
  margin-bottom: 10px;  
}  

.check-icon {  
  color: green;  
  font-size: 24px;  
  margin-right: 10px;  
}  

.popup-subtitle {  
  font-size: 14px;  
  color: gray;  
  margin-bottom: 15px;  
}  

.order-summary {  
  list-style: none;  
  padding: 0;  
  margin: 10px 0;  
}  

.order-summary li {  
  margin: 5px 0;  
}  

.total {  
  font-weight: bold;  
  margin: 10px 0;  
}  

.start-order-button {  
  background-color: #FF6F61; /* Cor laranja */  
  color: white;  
  border: none;  
  border-radius: 5px;  
  padding: 10px 15px;  
  cursor: pointer;  
  margin-top: 10px;  
}  

.start-order-button:hover {  
  background-color: #FF4F3D; /* Cor laranja mais escura */  
}  

.close-button {  
  background-color: transparent;  
  color: gray;  
  border: none;  
  cursor: pointer;  
  margin-top: 5px;  
}

.titleList {  
  font-size: 2rem;  
  color: #333;  
  margin-bottom: 20px;  
  padding-left: 15px;  
}  
.carbon-neutral{
  opacity: 1;
  background-color: #fcf9f7;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  gap: 5px;
  
  span{
    font-weight: 700;
  }
}
.container {  
  display: flex;  
  justify-content: space-between;   

  .minicartProducts {  
    width: 100%;   
    max-width: 420px;   
    min-width: 300px;   
    margin: 0 auto;   
    padding: 20px;   
    background-color: #fff;   
    border-radius: 12px;   
    height: fit-content;
    position: relative
  }
  .confirm-order{
    background-color: #c73a0f;
    max-width: 388px;
    height: 3rem;
    border-radius: 1.5rem;
    font-size: 1rem;
    line-height: 1.5rem;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    color: #fff;  
    border: none;  
    padding: 10px;  
    cursor: pointer;  
    width: 100%;  
    transition: background-color 0.3s; 
  }  
}  

.listProducts {  
  display: grid;  
  grid-template-columns: repeat(3, 1fr);  
}  

ul {  
  list-style-type: none;  
  padding: 0;  
}  

.box-product {  
  border-radius: 12px;  
  padding: 10px;  
  max-width: 270px;  
  text-align: center;  
  transition: transform 0.2s;
  position: relative;  

  .image-button-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}  

.box-product:hover {  
  transform: translateY(-5px);  
}  

.product-image {  
  max-width: 100%;  
  height: auto;  
  border-radius: 12px;  
}  

.product-info {  
  display: flex;  
  flex-direction: column;  
  align-items: flex-start;  

  .categoryName {  
    font-size: 12px;  
    color: #c4b9b6;  
    font-weight: 600;  
  }  
  
 
  .price {  
    color: #c73a0f;
    font-weight: bold;            
  }  
  
  > h2 {  
    font-size: 1rem;   
    font-weight: 600;  
    margin: 0px;  
  }  
}  

.add-to-cart {  
    color: black;
    border-radius: 20px;
    padding: 10px 15px;
    cursor: pointer;
    transition: background-color 0.3s;
    width: 170px;
    height: 40px;
    border: 1px solid black;
    background: white;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: -19px;

  img {  
    margin-right: 5px;  
  }  
}  

.productsCart{
    display: flex;
    flex-direction: column;
    .itemTitleCart{
      font-size: 14px;
      line-height: 1.25rem;
      font-weight: 700;
      margin: 0px;
    }
.quantityCartItem{
    color: #c73a0f;
    font-weight: 500;
    font-size: .875rem; 
}
.priceCartItem{
    color:#ad8985;
    font-weight: 400;
    font-size: 1rem;;
}
.totalPriceCartItem{
    color:#87635a;
    font-weight: 600;
    font-size: 1rem;;
}
    span{
    display: flex;
    gap: .5rem;
    align-items: center;
    }
    .removeProduct{
        background: none;
        border: 0px;
        position: absolute;
        right: 23px;
        border: 1px solid gray;
        border-radius: 50%;
        padding: 7px;
        width: 14px;
        height: 14px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.quantity {  
  display: flex;
  align-items: center;
  margin: 10px 0;
  width: 142px;
  height: 40px;
  justify-content: space-between;
  background: #c83c0e;
  border-radius: 25px;
  padding: 0px 14px;

  flex-direction: row-reverse;
  position: relative;
  top: -21px;
  margin: 0px;

  span {  
    color: white;  
  }  
}  

.quantity button {  
  color: #fff;  
  border: none;  
  border-radius: 8px;  
  cursor: pointer;  
  transition: background-color 0.3s;  
  background: transparent;  
  border: 1px solid;  
  border-radius: 50%;  
  height: 24px;  
  
  img {  
    vertical-align: middle;  
  }  
}  

.minicartProducts h2 {  
  font-size: 1.875rem;
  color: #c73a0f;
  margin: 0px;  
  font-weight: 700;
}  

.minicartProducts ul {  
  list-style-type: none;  
  padding: 0;  
}  

.minicartProducts ul li {  
  padding: 10px 0;  
  border-bottom: 1px solid #eee;  
}  

.total {  
  font-size: 1.2rem;  
  font-weight: bold;  
  margin-top: 10px;  
}  


.confirm-order:hover {  
  background-color: #e55b4c;  
}
.total{
    font-size: 14px;
    font-weight: 400;
    display: flex;
    justify-content: space-between;

    span{
        font-weight: bold;
        font-size: 1.5rem;
    }
}

@media (max-width:1400px){
    .box-product {
        max-width: 250px;
    }

    .container .minicartProducts{
        max-width: 300px;
        
    }
}

@media (max-width:1300px){

    .listProducts{
        grid-template-columns: repeat(2, 1fr);
    }
  
}
@media(max-width:1050px){
  .container{
    flex-wrap: wrap;
  }
}
@media (max-width:680px){
    .container{
        padding: 10px;
    }
    body{
        padding: unset
    }

    .listProducts{
        grid-template-columns: repeat(1, 1fr);
        margin: 0 auto;
    
    }
    .mainContainer{
        margin: 0 auto;
        display: flex;
        justify-content: center;
        flex-direction: column;
        max-width: 100%;
       
    }
    .box-product{
        border-radius: 12px;
        text-align: center;
        transition: transform 0.2s;
        position: relative;
        max-width: unset;
    }
    .container .minicartProducts{
        min-width: unset;
        max-width: unset;
    }

}
